import React from 'react';
import styled from 'styled-components';
import { Container, Row, Col } from 'react-lime-grid';
import { Helmet, Header, Footer, Logo, Demo, Code } from '../components/';
import { viewport } from '../constants';

console.log(global);

const Hero = styled.div`
  margin: 180px 0;
  text-align: center;

  & div {
    line-height: 1;
  }

  & svg {
    width: 40px;
    height: 55px;
  }

  & h1 {
    margin: 10px 0 0;
    font-size: 1rem;
  }

  & p {
    margin: 10px 0 0;
    font-size: 0.68rem;
  }

  @media (${viewport.md}) {
    & svg {
      width: 50px;
      height: 67.5px;
    }

    & h1 {
      font-size: 1.75rem;
    }

    & p {
      font-size: 0.875rem;
    }
  }
`;

const Section = styled.section`
  margin: 180px 0;

  & h2 {
    font-size: 1.75rem;
  }

  @media (${viewport.md}) {
    & h2 {
      font-size: 2.25rem;
      text-align: center;
    }
  }
`;


/* eslint-disable */
const ComponentItem = ({
  href,
  title,
}) => (
  <Col xs={12} sm={6} lg={4}>
    <img
      src={`${process.env.__PATH_PREFIX__}/components/react-drip-form-components.png`}
      alt={title}
      title={title}
    />
    <a
      href={href}
      title={title}
    >
      {title}
    </a>
  </Col>
);


export default () => (
  <div>
    <Helmet title="Powerfully React forms state manager" />

    <Header />

    <Hero>
      <Container>
        <div>
          <Logo />
        </div>
        <h1>react drip form</h1>
        <p>Powerfully React forms state manager</p>
      </Container>
    </Hero>

    <Section>
      <Demo />
    </Section>

    <Section>
      <Container>
        <h2>Features</h2>
        <Row center="xs">
          <Col xs={12} md={8}>
            <ul>
              <li>HOC based API. (No magic, transparent and open API)</li>
              <li>Free component design. Integration with many UI frameworks.</li>
              <li>Rule based validation, and Provide many built-in rules.Support async and sync validation.</li>
              <li>Support normalization.</li>
              <li>Support Nest fields and Array fields.</li>
              <li>Customizable error message. (Support i18n)</li>
            </ul>
          </Col>
        </Row>
      </Container>
    </Section>

    <Section>
      <Container>
        <h2>Installation</h2>
        <Row center="xs">
          <Col xs={12} md={8}>
            <Code language="bash">{'$ npm install react-drip-form --save'}</Code>
          </Col>
        </Row>
      </Container>
    </Section>

    <Section>
      <Container>
        <h2>Components</h2>
        <Row center="xs">
          <ComponentItem
            href="https://github.com/tsuyoshiwada/react-drip-form-components"
            title="Official UI Components"
          />
        </Row>
      </Container>
    </Section>

    <Footer />
  </div>
);



